
import { InversifyExpressServer } from "inversify-express-utils";
import puppeteer, { BrowserContext, BrowserLaunchArgumentOptions, Page, Puppeteer, PuppeteerNode, PuppeteerNodeLaunchOptions, Browser as PuppeteerBrowser } from 'puppeteer';
import { getPuppeteerArgs } from "../utils/base"; 

export class Browser {
  private url!: string
  private page!: Page
  private browser!: PuppeteerBrowser | BrowserContext;
  closePage!: () => Promise<void>
  constructor(url: string
  ) {
    this.url = url
  }

  async initBrowser() {
    await this.createBrowser()
    Promise.all([
      await this.page.goto(this.url, {
        waitUntil: 'networkidle2',
      }),
      // this.page.waitForSelector('.translation'),
      // await this.page.waitForNavigation()
    ])
 
  }

  private async createBrowser() {
    const puppeteerNode = (puppeteer as unknown as PuppeteerNode)
    const browser = await this.getBrowserInstance(puppeteerNode);
    const context = await browser.createIncognitoBrowserContext();
    const currentContext = this.url ? context : browser;

    this.browser = currentContext
    this.page = await currentContext.newPage();
    this.closePage = async () => {
     
        await browser.close();
     
    }
  }

  private getBrowserInstance(puppeteer: PuppeteerNode,
    //  browserURL?: string,
     rendererOptions: BrowserLaunchArgumentOptions = {}) {
    // if (browserURL) {
    //   return puppeteer.connect({
    //     browserURL,
    //   });
    // }
    return puppeteer.launch({
      args: getPuppeteerArgs(),
      // headless: false,
      // devtools: true,
      ...rendererOptions
    });
  }

  async getWindowData() {
    // const propsObj = {};
    // const context = { propsObj, windowProps };
    // console.log(cb)
    // const cbFunc = cb
    // console.log(cbFunc)
   

    const windowData = await this.page.evaluate(async () => {
      // for (const prop of context.windowProps) {
        // const clonedObject = await clone(window[prop]);
        // context.propsObj[prop] = clonedObject;
      // }
      // const windowClone = await clone(window.document)
      // // return context.propsObj;
      // // alert(cbFunc)
  
      // return window.cb()
      return  [...window.document.querySelectorAll('.translation')].filter((el: any) => el.title).map((elem: any) => {
        const titleDOM = new DOMParser().parseFromString(elem.title, "text/html").querySelector('.nobold') as Element
        let translate = ''
        let partsOfSpeech: string[] = []
        console.log(  titleDOM.childNodes)
   
        titleDOM.childNodes.forEach((node: any, index: number) => {
          if (node.innerText) translate = node.innerText
          if (index === 2) {
            if (node.textContent.includes('/')) {
              node.textContent.split('/').forEach((speech: string) => partsOfSpeech.push(speech.trim().toLowerCase()))
              return
            }
            partsOfSpeech.push(node.textContent.split(' ')[0].toLowerCase())
          }
        })
       

        return {
          translate,
          partsOfSpeech
        }
      })
    });

    return windowData;
  }
  // [...window.document.querySelectorAll('.translation')].map((elem: any) => console.log(elem.title))
}



