
import { User } from "../models/User";
import { updateUser } from "../utils/base";


import { controller, httpPatch, httpGet, httpDelete } from "inversify-express-utils";
import { authMiddleWare } from "../middleware/authMiddleware";
import { Friend } from "../models/Friend";
import { Room } from "../models/Room";
import mongoose from "mongoose";
import { myContainer } from "../containerDI";
import { TYPES } from "../interfaces";
import { WsServer } from "../modules/WsServer";
import { Browser } from "../modules/Browser";
import { inject } from "inversify";

@controller("/api/translate", authMiddleWare)
export class TranslateController {
  constructor(@inject(TYPES.Browser) private browser: typeof Browser) { }

  @httpGet('/translate')
  async translate(request: any, response: any) {

    const { idUser } = request._user;
    const { language, translatedLanguage, word } = request.query
 
    // english language - russian translatedLanguage
    const browser = new this.browser(`https://context.reverso.net/translation/${language}-${translatedLanguage}/${word}`)
    console.log('before init browser')
    await browser.initBrowser();
    const data = await browser.getWindowData()
    console.log('after init browser')
    console.log(data)
    await browser.closePage()

    return response.json(data)
  }

}


